<template>
  <el-container>
    <el-header>
      <el-breadcrumb separator="/">
        <el-breadcrumb-item v-for="(item,idx) in currentPath"
                            :key="idx">{{item}}</el-breadcrumb-item>
      </el-breadcrumb>
    </el-header>
    <el-main>
      <el-table :data="files"
                style="width: 100%"
                @row-dblclick="clickRowHandle">
        <el-table-column prop="name"
                         label="文件名"
                         width="300">
        </el-table-column>
        <el-table-column prop="fileType"
                         label="文件类型"
                         width="180">
        </el-table-column>
        <el-table-column prop="size"
                         :formatter="sizeFormatter"
                         label="文件大小">
        </el-table-column>
        <el-table-column prop="modifyTime"
                         width="150"
                         label="修改时间">
        </el-table-column>
        <el-table-column prop="accessUrl"
                         width="450"
                         label="下载地址">
          <template slot-scope="scope">
            <a :href="scope.row.accessUrl">{{scope.row.accessUrl}}</a>
          </template>
        </el-table-column>
      </el-table>
    </el-main>

  </el-container>
</template>
<script>
import request from '../utils/request'

export default {
  data () {
    return {
      files: [],
      currentPath: ['index']
    }
  },
  created () {
    this.fetchData('')
  },
  methods: {
    sizeFormatter (row) {
      return `${row.size}B`
    },
    clickRowHandle (row) {
      if (row.fileType == "directory") {
        this.fetchData(row.name)
        this.currentPath.push(row.name)
      }
    },
    fetchData (path) {
      request({ method: 'get', url: `api/listResource?path=${path}` }).then(res => this.files = res.context)
    }
  }
}
</script>
<style scoped>
.el-header {
  height: 20px !important;
}
</style>


