<template>
  <el-container>
    <el-table :data="commands"
              style="width: 100%">
      <el-table-column prop="id"
                       label="id"
                       width="50">
      </el-table-column>
      <el-table-column prop="command"
                       label="命令"
                       width="250">
      </el-table-column>
      <el-table-column prop="type"
                       label="类型"
                       width="180">
      </el-table-column>
      <el-table-column prop="workDir"
                       label="工作路径">
      </el-table-column>
      <el-table-column fixed="right"
                       label="操作"
                       width="100">
        <template slot-scope="scope">
          <el-button @click="open(scope.row)"
                     type="text"
                     size="small">执行</el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-dialog title="执行结果"
               :visible.sync="dialogVisible"
               width="60%"
               :show-close="false"
               :before-close="handleClose">
      <div v-for="(item,idx) in result"
           :key="idx">
        <h4>命令：{{item.command}} 工作路径：{{item.workDir}}<br></h4>
        <span v-for="(line,index) in item.resultInfo"
              :key="index">{{line}} <br></span>
      </div>
      <span slot="footer"
            class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary"
                   @click="dialogVisible = false">确 定</el-button>
      </span>
    </el-dialog>
  </el-container>
</template>
<script>
import request from '../utils/request'
import md5 from 'js-md5'
export default {
  data () {
    return {
      dialogVisible: false,
      commands: [],
      result: [],
    }
  },
  created () {
    request({ method: 'get', url: 'api/listCommand' }).then(res => this.commands = res)
  },
  methods: {
    open (row) {
      this.current = row
      if (row.open) {
        this.executeCommandHandle(row.id)
      } else {
        this.$prompt('请输入密码', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
        }).then(({ value }) => {
          this.executeCommandHandle(row.id, value)
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '取消执行'
          });
          this.handleClose()
        });
      }
    },
    executeCommandHandle (id, password) {
      request.post(`api/executeCommand`, {
        id: id,
        password: md5(password)
      }).then(res => {
        this.result = res
        this.dialogVisible = true
      }).catch(error => console.log(error))
    },
    handleClose () {
      this.result = []
    }
  }
}
</script>


